import {ADD_REMINDER, REMOVE_REMINDER ,CLEAR_ALL} from '../actions/types';
import {bake_cookie, read_cookie} from 'sfcookies'

const reminders = (state=[], action) =>{
    
    state =read_cookie('reminders')
    
    
    let reminders = [];
    if(action.type === ADD_REMINDER){
        reminders = [...state, {reminder: action.reminder, date: action.date, id:Math.random()}];
        
        bake_cookie('reminders', reminders);
        return reminders;
    }
    else if(action.type === REMOVE_REMINDER){
        reminders = state.filter( i => i.id !== action.id );
        bake_cookie('reminders', reminders);

        return reminders
    }
    else if(action.type === CLEAR_ALL){
        reminders= [];
        bake_cookie('reminders', reminders);

        return reminders
    }
    else{
        return state
    }
}
export default reminders