import React, {Component, Fragment} from 'react';
import { Title, Input, Button, Cont, Img, Span, Li } from './style';
import {connect} from 'react-redux';
import {add_reminder, remove_reminder, clear_all} from '../actions';
import moment from 'moment';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import '../App.css';
import logo from './reminder.png';

class App extends Component{
    state={
        reminder: '',
        date: new Date()
    }

    render(){
        const allreminders = this.props.reminders.map((i) =>{ 
            return(
                <ul key={i.id} className="list-group" >
                    <Li className="list-group-item">
                        <div>{ i.reminder }</div>
                        <div>{moment(i.date).fromNow() }</div>
                        <Span className="btn btn-danger" 
                              onClick={()=> this.props.remove_reminder(i.id) } >x</Span>
                    </Li>
                </ul>
            ) });
        return(
            <Cont>
                <Title>what should you do?</Title>
                <Img src= {logo} />
                <Input
                     type="text" 
                     placeholder=" Your Reminder..." 
                     className="form-control" 
                     value={this.state.reminder}  
                     onChange= { (e) => this.setState({ reminder: e.target.value }) } />
                {/* <Input 
                    type="datetime-local" 
                    className="form-control" 
                    value={this.state.date}
                    onChange= { (e) => this.setState({ date: e.target.value }) } /> */}

                <DatePicker 
                    selected={this.state.date}
                    className="form-control date_picker" 
                    value={this.state.date}
                    onChange={(date) => this.setState({date})}
                    showTimeSelect
                    timeFormat="HH:mm"
                    timeIntervals={15}
                    timeCaption="time"
                    dateFormat="MMMM d, yyyy h:mm aa"
                    />

                <Button first
                    className="btn btn-primary btn-block" 
                    onClick= { ()=>{ if(this.state.reminder !==''){
                        this.props.add_reminder(this.state.reminder, this.state.date) ;
                        this.setState({reminder:''})} } } 
                     >Add reminder</Button>
                <Button second
                    className="btn btn-danger"
                    onClick={ this.props.clear_all } >Clear All</Button>
                {allreminders}
            </Cont>
        )
    }
}
function mapStateToProps(state){
    return{
        reminders: state
    }
}

// function mapDispatchToProps(dispatch){
//     return{
//         add_reminder: () => dispatch(add_reminder())
//     }
// }

export default connect(mapStateToProps,{add_reminder, remove_reminder, clear_all})(App);