import {ADD_REMINDER, REMOVE_REMINDER, CLEAR_ALL} from './types';

export const add_reminder = (reminder, date) =>{
    const action ={
        type: ADD_REMINDER,
        reminder,
        date
    }
    return action
}

export const remove_reminder =(id) =>{
    const action={
        type: REMOVE_REMINDER,
        id
    }
    return action
}
export const clear_all =() =>{
    const action={
        type: CLEAR_ALL,
    }
    return action
}